import React, { useState } from 'react';
import {connect} from 'react-redux'
import {NavLink} from 'react-router-dom'
import AccessibleForwardIcon from '@material-ui/icons/AccessibleForward'

import './header.scss'


const Header = (props) => {

    const [active, setActive] = useState(false)
    return (
        <header className="header">
            <div className="flex">
                <NavLink to='/'><div onClick={() => active ? setActive(!active) : null}  className="logo">Invo</div> </NavLink>
                <div className={active ? "menu-container active" : "menu-container"}>
                    <nav>
                        <ul className="navBar">
                            {
                                props.listItems.map((item, index) => {
                                    return <li key={index} onClick={() => setActive(!active)} className="navItem"><NavLink  to={item.path} activeStyle={{ color: '#333'}} className="nav_href">{item.name}</NavLink></li>
                                })
                            }
                        </ul>
                    </nav>
                
                <div className="login-wrapper">
                <NavLink to='/login' className="enter">
                    <AccessibleForwardIcon fontSize="large" />
                    <p className="enter__text">Войти</p>
                </NavLink>
                </div>
                </div>
            </div>
            <div className={active ? "burger_menu active" : "burger_menu"} onClick={() => setActive(!active)}>
                <img src="./assets/img/arrow-right-menu.png" className="burger-img" alt="Image" />
            </div>
        </header>
    )
}


const mapStateToProps = state => {
    return {
      listItems: state.header.header
    };
  };
  
  export default connect(mapStateToProps)(Header);
  