import React from 'react'
import {connect} from 'react-redux'

import {Pagination, ForumMessage, SearchBar, NewMessage, ForumSidebar} from '../../components'

import './forum.scss'


const Forum = (props) => {
    return (
        <div>
            <SearchBar />
            <div className="wrapper __padding">
                <div className="forum__container">
                <Pagination quantity={5} active={1} />
                <div className="forum__main">
                    <div className="forum__main_left">
                        
                        <NewMessage />

                        <div className="forum__comments">
                            {
                                props.listItems.map((item, index) => {
                                    return <ForumMessage key={index} userName={item.userName} message={item.message} date={item.date} />
                                })
                            }
                        </div>
                    </div>
                    <ForumSidebar />
                </div>
            </div>
            </div>
        </div>
    )
}


const mapStateToProps = state => {
    return {
      listItems: state.forum.forum
    };
  };
  
  export default connect(mapStateToProps)(Forum);
  
